import { Component,ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams,AlertController,Platform  } from 'ionic-angular';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Camera, CameraOptions } from '@ionic-native/camera';

import { ClaimServiceProvider } from '../../providers/claim-service/claim-service';
import { UploadPage } from '../upload/upload';
import { mobiscroll, MbscCalendarOptions,MbscDatetimeOptions  } from '@mobiscroll/angular';
import { GeocoderProvider } from '../../providers/geocoder/geocoder';
import { Geolocation } from '@ionic-native/geolocation';


/**
 * Generated class for the ClaimPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
mobiscroll.settings = {
  theme: 'ios'
};

let now = new Date();

@IonicPage()
@Component({
  selector: 'page-claim',
  templateUrl: 'claim.html',
})
export class ClaimPage {
  policyid: any;
  claimdata: any;
  claimForm: FormGroup;
  claim = new Claim();
  defaultimg : string = "../../assets/imgs/logo.png";
  time: Date = now;
  h12: Date = now;
  h24: Date = now;
  hms: Date = now;
    external: Date = new Date();
    testsrc:string;

    h12Settings: MbscDatetimeOptions = {
      timeFormat: 'hh:ii:ss A'
  };
    externalSettings: MbscCalendarOptions = {
        showOnTap: false,
        showOnFocus: false
    };

    settings = {
      theme: 'ios'
  };
  @ViewChild('inputtime') inputtime;

  public vehicleOptions: string[] = [
    "2010 Suzuki Baleno",
    "Maruti Suzuki Dzire"
  ];

  public causeOptions: string[] = [
    "Collision",
    "Third Party Liability",
    "Comprehensive"
  ];

  public subCauseOptions: string[] = [
    "Hitting a car",
    "Hitting a pedestrian",
    "Hitting a lamp post",
    "Flying debris"
  ];
  testvar :any;
  constructor(public navCtrl: NavController, public navParams: NavParams, private claimservice: ClaimServiceProvider, 
    private formBuilder: FormBuilder, private camera: Camera, private alertCtrl: AlertController,
    public _GEOCODE   : GeocoderProvider,
    private _PLATFORM : Platform,private geolocation: Geolocation
  ) {
    // ,private fileChooser: FileChooser,private filePath: FilePath
   this.bindData();
    this.claim.vehicle = this.navParams.get('vehicleName');
    this.claim.lossdate = new Date().toLocaleString('en-US').split(', ')[0];
    this.claim.losstime = new Date().toLocaleString('en-US').split(', ')[1];
    this.claim.losslocation = "";
    
    this.claimForm = this.formBuilder.group({
      vehicle: ['', Validators.required],
      lossdate: ['', Validators.required],
      losstime: ['', Validators.required],
      sublosscause: ['', Validators.required],
      losslocation: ['', Validators.required],
    });
    this.initMap();    
  };
  performReverseGeocoding(latitude     : any,longitude    : any)
   {
      this._PLATFORM.ready()
      .then((data : any) =>
      {
         this._GEOCODE.reverseGeocode(latitude, longitude)
         .then((data : any) =>
         {         
          //this.XAlert('data',data);
          this.claim.losslocation = data; 
         })
         .catch((error : any)=>
         {
          this.XAlert('performReverseGeocoding error',error);
         });
      });
   }
  XAlert(title:string,subTitle:string) {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: subTitle,
      buttons: [{
        text: 'OK',
        handler: () => {
        }
      }]
    });
    alert.present();
  }
  submitNext(){
    this.claim.vehicle = this.claimForm.controls['vehicle'].value
    this.claim.lossdate = this.claimForm.controls['lossdate'].value
    this.claim.losstime = this.claimForm.controls['losstime'].value  
    this.claim.sublosscause = this.claimForm.controls['sublosscause'].value
    this.claim.losslocation = this.claimForm.controls['losslocation'].value
    var claimData = JSON.stringify(this.claim);    
    console.log(claimData);
    this.navCtrl.push(UploadPage,claimData);
  }
  bindData(){
    this.policyid = this.navParams.get('policyid');
    this.claimdata = {"imagepath":[this.defaultimg,this.defaultimg,this.defaultimg,this.defaultimg],"_id":"5b6f928d3d5c3b90ee479783","policyid":"P001","claimid":"C001","model":"2010 Suzuki Baleno","lossdate":"2018-08-12","losstime":"","losscause":"Collision","sublosscause":"Hitting a car","iscamera":true};
    // this.claimservice.getDetailsByPolicyId("5b6f8ee753a8e609fe43d558").then(data=>{
    //   console.log("getDetailsByPolicyId");
    //   console.log( data);
    // });
  } 

  initMap() {
    this.geolocation.getCurrentPosition({ maximumAge: 3000, timeout: 15000, enableHighAccuracy: true }).then((resp) => {
      
      //this.XAlert('data',resp.coords.latitude + ' data ' + resp.coords.longitude);
      this.performReverseGeocoding(resp.coords.latitude,resp.coords.longitude);  
    }).catch((error: any) =>
    {
       console.log(error);
       this.XAlert('getCurrentPosition error',error.message);
    });;
  }  
   
  openTime(){
    this.inputtime.setFocus();
    this.inputtime.setFocus();
  }
}
export class Claim {
  vehicle: string;
  lossdate: String;
  losstime:string;
  sublosscause: String;
  losslocation:string;
}



