import { HttpClient,HttpHeaders  } from '@angular/common/http';
import { AlertController } from 'ionic-angular';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/toPromise';
import { map } from 'rxjs/operators';

/*
  Generated class for the ClaimServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ClaimServiceProvider {
apiUrl = "http://13.232.237.186:9090";
demoUrl = "https://jsonplaceholder.typicode.com/todos/1";
  constructor(public http: HttpClient,private alertCtrl: AlertController) {
    console.log('Hello ClaimServiceProvider Provider');
  }
getPolicyList(){
  return new Promise(resolve=>{
    this.http.get(this.apiUrl + "/policies").subscribe(data=>{
      resolve(data);
    },err=>{console.log(err);})
  })
}
getDetailsByPolicyId(policyid:string){
  return new Promise(resolve=>{
    this.http.get(this.apiUrl + "/policies/" + policyid).subscribe(data=>{
      resolve(data);
    },err=>{console.log(err);})
  })
}
 postImage(imgdata:string){
  return  new Promise(resolve=>{
    var params = JSON.stringify({ data:imgdata});
    const httpOptions = { headers: { 'Content-Type': 'application/json'} };
    this.http.post("https://mid16o70gc.execute-api.us-east-1.amazonaws.com/api/", params, httpOptions).subscribe(data=>{
      resolve(data);   
    },err=>{console.log(err);})
  })
 }
 
 
 SAlert(title:string,subTitle:string) {
  let alert = this.alertCtrl.create({
    title: title,
    subTitle: subTitle,
    buttons: [{
      text: 'OK',
      handler: () => {
      }
    }]
  });
  alert.present();
}
}



