import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController } from 'ionic-angular';
import { ChooseservicePage } from '../chooseservice/chooseservice';
import { ClaimServiceProvider } from '../../providers/claim-service/claim-service';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/toPromise';
import { map } from 'rxjs/operators';


/**
* Generated class for the ResultPage page.
*
* See https://ionicframework.com/docs/components/#navigation for more info on
* Ionic pages and navigation.
*/


@IonicPage()
@Component({
  selector: 'page-result',
  templateUrl: 'result.html',
})

export class ResultPage {

  totalDamageIndex: number;
  damageToTimelineRatio: number[] = [
    2,
    4,
    10,
    15,
    25
  ];

  damageTimeline: number;
  frontDamage: number=0;
  leftDamage: number=0;
  rightDamage: number=0;
  rearDamage: number=0;
  totalDamage: number=0;

  frontDamageCostRatio: number[] = [
    200,
    600,
    1000,
    12000,
    15000
  ];

  leftDamageCostRatio: number[] = [
    50,
    100,
    180,
    260,
    500
  ];

  rightDamageCostRatio: number[] = [
    50,
    100,
    180,
    260,
    500
  ];

  rearDamageCostRatio: number[] = [
    100,
    250,
    500,
    800,
    1000
  ];

 

  frontDamageCost: number;
  leftDamageCost: number;
  rightDamageCost: number;
  rearDamageCost: number;

  totalDamageCost: number;
data:any;
testdata:any;
damageType:string;
flatCharge:number;
workType:string;
repairTime:number;
labourCost:number;
partsCost:number;
prymaryCost:number;
totalLabourCost:number;
totalCost:number;
allTotalCost:number;
allTotalPartsCost:number;
allTotalLabourCost:number;
timeType:string = "Days";
damage:any=[{imgbase64:"", damagePart:"",damagePer:0,damageType:"",workType:"",partsCost:0,prymaryCost:0,repairTime:0,labourCost:0,totalCost:0 }];
  constructor(public navCtrl: NavController, public navParams: NavParams,private alertCtrl: AlertController, private claimservice: ClaimServiceProvider) {
     this.data = JSON.parse(this.navParams.data);
     if(this.data.frontimgper>0){
       this.SAlert('Front',this.data.frontimgper.toString())
     }if(this.data.backimgper>0){
      this.SAlert('Back',this.data.backimgper.toString())
    }if(this.data.leftimgper>0){
      this.SAlert('Left',this.data.leftimgper.toString())
    }if(this.data.rightimgper>0){
      this.SAlert('Right',this.data.rightimgper.toString())
    }
     if(this.data.frontimg!=""){
      this.frontDamage=(100-this.data.frontimgper*100);
     }
    if(this.data.backimg!=""){
        this.rearDamage = (100-this.data.backimgper*100);
      }
    if(this.data.leftimg!=""){
        this.leftDamage = (100 - this.data.leftimgper*100);
    }
    if(this.data.rightimg!=""){
        this.rightDamage =(100 - this.data.rightimgper*100);
     }
    //  this.data.frontimg="data:image/jpeg;base64,";
    //  this.frontDamage=5;
    //  this.data.backimg="data:image/jpeg;base64,";
    //  this.rearDamage=10;
    //  this.data.leftimg="data:image/jpeg;base64,";
    //  this.leftDamage=30;
    //  this.data.rightimg="data:image/jpeg;base64,";
    //  this.rightDamage=60;
this.Calculate();
  }

  



  //-------------------------------------------------------------------------------
  defineTypes(imgbase64:string, damagePart:string, percentage:number){
if(percentage>0 && percentage<=20){
  this.damageType ="Minor";
  this.workType="Repair";
  this.repairTime = 6;
  this.labourCost = 350;
  this.partsCost = 0;
  this.flatCharge=1500;
}
else if(percentage>20 && percentage<=50){
  this.damageType ="Medium";
  this.workType="Repair";
  this.repairTime = 10;
  this.labourCost = 350;
  this.partsCost = 0;
  this.flatCharge=1500;
}
else if(percentage>20 && percentage<=50){
  this.damageType ="Major";
  this.workType="Replace";
  this.repairTime = 12;
  this.labourCost = 350;
  this.partsCost = 35;
  this.flatCharge=1500;
}
else{
  this.damageType ="No Damage";
  this.workType="Nothing";
  this.repairTime = 0;
  this.labourCost = 0;
  this.partsCost = 0;
  this.flatCharge=0;
}
this.prymaryCost=(this.flatCharge + (this.flatCharge*(this.frontDamage/100)));
this.totalLabourCost=(this.repairTime*this.labourCost);
this.totalCost = (this.partsCost + this.prymaryCost + this.totalLabourCost);
if(this.totalCost!=0){
this.damage.push({
imgbase64: imgbase64,
damagePart:damagePart,
damegePer:percentage,
damageType:this.damageType,
workType:this.workType,
partsCost:this.partsCost,
prymaryCost:this.prymaryCost,
repairTime:this.repairTime,
labourCost:this.totalLabourCost,
totalCost:this.totalCost});
}
  }

Calculate(){
  this.defineTypes(this.data.frontimg,"Front",this.frontDamage);
  this.defineTypes(this.data.backimg,"Back",this.rearDamage);
  this.defineTypes(this.data.leftimg,"Left",this.leftDamage);
  this.defineTypes(this.data.leftimg,"Right",this.rightDamage);
  this.damage.splice(0, 1);
  this.totalDamageCost = 0;
  this.damageTimeline=0;
  this.allTotalPartsCost=0;
  this.allTotalLabourCost=0;
  
  let minordamageTimeline : number=0;
  let mediumdamageTimeline : number=0;
  let majordamageTimeline : number=0;

  for(let item of this.damage) {
    this.totalDamageCost = this.totalDamageCost +  item.totalCost;
    this.allTotalPartsCost = this.allTotalPartsCost + item.partsCost;
    this.allTotalLabourCost = this.allTotalLabourCost + item.labourCost;

    if(item.damageType=="Minor")
    {
      minordamageTimeline = minordamageTimeline + item.repairTime;
    }
    else if(item.damageType=="Medium")
    {
      mediumdamageTimeline = mediumdamageTimeline + item.repairTime;
    }
    else if(item.damageType=="Major")
    {
      majordamageTimeline = majordamageTimeline + item.repairTime;
    }
  }
  
  if(minordamageTimeline!=0){
    this.damageTimeline = minordamageTimeline;  
    this.timeType ="Days";
    }
  if(mediumdamageTimeline!=0){
    this.damageTimeline = mediumdamageTimeline;  
    this.timeType ="Weeks";
    }
  if(majordamageTimeline!=0){
  this.damageTimeline = majordamageTimeline;  
  this.timeType ="Weeks";
  }

  console.log(this.totalDamageCost);
}
  //-------------------------------------------------------------------------------
  async delay(ms: number) {
    await new Promise(resolve => setTimeout(()=>resolve(), ms)).then(()=>console.log("fired"));
}
 
  ionViewDidLoad() {
    console.log('ionViewDidLoad ResultPage');
  }
  presentConfirm() {
    let alert = this.alertCtrl.create({
      title: 'Confirm Submission',
      message: 'Do you still want to submit the claim?',
      buttons: [
        {
          text: 'No',
          role: 'cancel',
          handler: () => {
          }
        },
        {
          text: 'Yes',
          handler: () => {
          
          this.XAlert("Claim Successfully Saved!!!","Your Claim Id is CLM001");
          }
        }
      ]
    });
    alert.present();
  }
  XAlert(title:string,subTitle:string) {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: subTitle,
      buttons: [{
        text: 'OK',
        handler: () => {
          this.navCtrl.push(ChooseservicePage);
        }
      }]
    });
    alert.present();
  }

  SAlert(title:string,subTitle:string) {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: subTitle,
      buttons: [{
        text: 'OK',
        handler: () => {
        }
      }]
    });
    alert.present();
  }
  goToRepairTime(){ 
    this.presentConfirm();
  }
}

 

 


