import { Component,ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams,AlertController } from 'ionic-angular';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Camera, CameraOptions } from '@ionic-native/camera';
//import { FileChooser } from '@ionic-native/file-chooser';

import { ClaimServiceProvider } from '../../providers/claim-service/claim-service';
import { ClaimDetailsPage } from '../claimdetails/claimdetails';
import { ResultPage } from '../result/result';
//import { mobiscroll, MbscCalendarOptions,MbscDatetimeOptions  } from '@mobiscroll/angular';
// import { FilePath } from '@ionic-native/file-path';


/**
 * Generated class for the ClaimPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
// mobiscroll.settings = {
//   theme: 'ios'
// };

let now = new Date();

@IonicPage()
@Component({
  selector: 'page-upload',
  templateUrl: 'upload.html',
})
export class UploadPage {
  policyid: any;
  claimdata: any;
  claimForm: FormGroup;
  claim = new Claim();
  base64Image : any;
  defaultimg : string = "../../assets/imgs/logo.png";
  time: Date = now;
  h12: Date = now;
  h24: Date = now;
  hms: Date = now;
    external: Date = new Date();
    testsrc:string;

  //   h12Settings: MbscDatetimeOptions = {
  //     timeFormat: 'hh:ii:ss A'
  // };
  //   externalSettings: MbscCalendarOptions = {
  //       showOnTap: false,
  //       showOnFocus: false
  //   };

    settings = {
      theme: 'ios'
  };
  @ViewChild('inputtime') inputtime;

  public vehicleOptions: string[] = [
    "2010 Suzuki Baleno",
    "Maruti Suzuki Dzire"
  ];

  public causeOptions: string[] = [
    "Collision",
    "Third Party Liability",
    "Comprehensive"
  ];

  public subCauseOptions: string[] = [
    "Hitting a car",
    "Hitting a pedestrian",
    "Hitting a lamp post",
    "Falling debris"
  ];
  constructor(public navCtrl: NavController, public navParams: NavParams, private claimservice: ClaimServiceProvider, 
    private formBuilder: FormBuilder, private camera: Camera, private alertCtrl: AlertController
  ) {
   this.bindData();
    this.claim.iscamera =true;
    this.claimForm = this.formBuilder.group({
    });
  };
  
  submitNext(){ 
    this.claim.leftimg = this.CheckDefault(document.getElementById("leftimg").getAttribute('src')); 
    this.claim.rightimg =this.CheckDefault(document.getElementById("rightimg").getAttribute('src')); 
    this.claim.frontimg =this.CheckDefault(document.getElementById("frontimg").getAttribute('src')); 
    this.claim.backimg =this.CheckDefault(document.getElementById("backimg").getAttribute('src')); 
    this.claim.leftimgper = document.getElementById("leftimg").getAttribute('val'); 
   this.claim.rightimgper =document.getElementById("rightimg").getAttribute('val'); 
   this.claim.frontimgper =document.getElementById("frontimg").getAttribute('val'); 
   this.claim.backimgper =document.getElementById("backimg").getAttribute('val'); 

  //  this.claim.leftimg="data:image/jpeg;base64,";
  //  this.claim.leftimgper = "0.2010";
  //  this.claim.rightimg="data:image/jpeg;base64,";
  //  this.claim.rightimgper = "0.2010";

   var claimData = JSON.stringify(this.claim); 
    //this.XAlert(this.claim.rightimg.toString());   
    console.log(claimData);
    this.navCtrl.push(ResultPage,claimData);
  }
  CheckDefault(img:string){
  if(img!=this.defaultimg)
  {
return img;
  }
  else {
return "";
  }
  }
  bindData(){
    this.claimdata = {"imagepath":[this.defaultimg,this.defaultimg,this.defaultimg,this.defaultimg],"_id":"5b6f928d3d5c3b90ee479783","policyid":"P001","claimid":"C001","model":"2010 Suzuki Baleno","lossdate":"2018-08-12","losstime":"","losscause":"Collision","sublosscause":"Hitting a car","iscamera":true};
    
  } 

  XAlert(body:string) {
    let alert = this.alertCtrl.create({
      title: "alert",
      subTitle:  body,
      buttons: ['Ok']
    });
    alert.present();
  }

  openCameraOrFileChooser(ctrl:string,iscamera:boolean){
if(iscamera){
  this.openCamera(ctrl);
}
else{
  this.openFileChooser(ctrl);
}
}
  
   openFileChooser(ctrl:string){
  const options: CameraOptions = {
    quality: 80,
    destinationType: this.camera.DestinationType.DATA_URL,
    encodingType: this.camera.EncodingType.JPEG,
    mediaType: this.camera.MediaType.PICTURE,
    saveToPhotoAlbum: true,
    sourceType:this.camera.PictureSourceType.PHOTOLIBRARY,
  }
  
  this.camera.getPicture(options).then((imageData) => {
   this.base64Image = 'data:image/jpeg;base64,' + imageData;

   this.claimservice.postImage(imageData).then(data=>{
    //this.XAlert(JSON.stringify(data));        
    var obj = JSON.parse(JSON.stringify(data));
   let cameraImageSelector = document.getElementById(ctrl);
   cameraImageSelector.setAttribute('val', obj.response);
   cameraImageSelector.setAttribute('src', this.base64Image);
  });

  }, (err) => {
    console.log(err);
  });
   }
 
  openCamera(ctrl:string){
    const options: CameraOptions = {
      quality: 80,
      destinationType: this.camera.DestinationType.DATA_URL,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: this.camera.MediaType.PICTURE,
      cameraDirection: this.camera.Direction.BACK,
      saveToPhotoAlbum: true,
    }
    
    this.camera.getPicture(options).then((imageData) => {
     this.base64Image = 'data:image/jpeg;base64,' + imageData;
    
     this.claimservice.postImage(imageData).then(data=>{
      //this.XAlert(JSON.stringify(data));        
      var obj = JSON.parse(JSON.stringify(data));
     let cameraImageSelector = document.getElementById(ctrl);
     cameraImageSelector.setAttribute('val', obj.response);
     cameraImageSelector.setAttribute('src', this.base64Image);
    });
    }, (err) => {
      console.log(err);
    });
  }
}
export class Claim {
  iscamera:boolean;
  leftimg: String;
  rightimg: String;
  frontimg: String;
  backimg: String;
  leftimgper: String;
  rightimgper: String;
  frontimgper: String;
  backimgper: String;
}



