@IonicPage()
@Component({
  selector: 'page-policy',
  templateUrl: 'policy.html',
})
export class PolicyPage {
  policies: any;
  vehicle: Vehicle;

  constructor(public navCtrl: NavController, public navParams: NavParams, private claimservice: ClaimServiceProvider, private alertCtrl: AlertController) {
    this.vehicle = new Vehicle();
    this.vehicle.name = "Maruti Suzuki Dzire";
    this.vehicle.image = "../../assets/imgs/maruti-suzuki-dzire-red.jpg";
    
    this.getPolicies();
  }
   getPolicies(){
    //  this.claimservice.getPolicyList().then(data=>{
    //    this.policies = data;
    //    console.log(data);
    //  });
    
    //  this.claimservice.postImage("").then(data=>{
    //    console.log(data);
    //    this.XAlert(data.response);
    //  });
   }  

   XAlert(body:string) {
    let alert = this.alertCtrl.create({
      title: "alert",
      subTitle:  body,
      buttons: ['Ok']
    });
    alert.present();
  }
  goToClaim(policyid:string){
    this.navCtrl.push(ClaimPage,{vehicleName : this.vehicle.name});
    //this.navCtrl.push(ClaimDetailsPage);
  }
}

class Vehicle {
  name: string;
  image: string;
}


